        text:
          "   If an asanbosam is positioned above a target (usually by using its Climb speed), it can make a claw Strike with its reach increased to 15 feet. This Strike deals an additional 1d6 slashing damage, and if it hits, the asanbosam Grabs the target and pulls it to a space adjacent to the asanbosam. This counts as two attacks when calculating the asanbosam\u2019s multiple attack penalty.",
