    text:
      "Asanbosams are monstrous, hairy humanoids with cold iron fangs and muscular limbs that end in powerful, hooked claws. They hide in treetops and grab prey from above, latching onto their unfortunate victims\u2019 necks to drain their blood. Locals in nearby communities tell horror stories of these \u201cbloodsucking tree-folk,\u201d and experienced jungle explorers keep a close watch on the treetops in asanbosam territory., An asanbosam\u2019s diet calls for a large amount of iron (typically in the form of blood) to maintain the integrity of its oversized cold iron teeth. When not hunting or mating, an asanbosam spends its waking hours chewing on certain types of rocks to sharpen its teeth., Some folk tales claim that asanbosams are as old as time and are the original source of cold iron, and that veins of the metal found in the ground are the buried graveyards of ancient asanbosams. Brave (or foolish) poachers often infiltrate asanbosam territory to hunt these monsters in the hope of collecting sacks of their valuable teeth.",
