    text:
      "Brontosauruses are truly gigantic behemoths, plodding herbivores large enough to be unafraid of all but the most massive predators. Brontosauruses have stout bodies and long, sinuous necks ending in small heads, allowing them to graze from the highest treetops. Their powerful tails are equally as long as their necks and provide counterbalances. Brontosauruses bear their bulk upon four stout legs. Although their feet are capable of crushing entire buildings, these herbivores are generally peaceful and considerate of where they step., Most brontosauruses are dangerous only when defending themselves or their young, or when panicked enough to stampede. When roused in this way, however, the creatures are truly terrifying: their muscular tails sweep in great arcs and their massive feet crash down heedlessly onto the ground. A brontosaurus is 70 feet long and weighs more than 30 tons., Brontosauruses belong to a group of dinosaurs known as sauropods, all of which are immense in size. The brachiosaurus is among the tallest of these dinosaurs, and the diplodocus among the longest, but it\u2019s likely that even larger behemoths exist.",
