        text:
          "   (air, arcane, evocation) Once per round. The djinni all creatures in its whirlwind pushes back 20 feet, or forces all creatures in the aura to move 20 feet clockwise or counterclockwise. Each creature must attempt a DC 21 Fortitude save. On a success, it avoids being moved, and on a critical failure it falls prone in addition to being moved. Creatures with the air trait are immune.",
