        text:
          "   (attack, move) The dragon turtle tries to capsize an adjacent aquatic vessel of its size or smaller. The dragon turtle must succeed at an Athletics check with a DC of 30 or the pilot\u2019s Sailing Lore DC, whichever is higher. ",
        actions: "Single Action",
