    "Independent Brains":
      " Each of an ettin\u2019s heads rolls its own initiative and has its own turn. Neither head can Delay. At the start of a head\u2019s turn, that head gets 2 actions and 1 reaction. Each brain controls one of the ettin\u2019s arms, but both can move the legs. Any ability that would sever an ettin\u2019s head (such as the  weapon property) doesn\u2019t cause the ettin to die if it still has its other head, but does cause it to lose the turns, actions, and reactions of the severed head. Mental effects that target a single creature affect only one of the ettin\u2019s heads.",
