        text:
          "   (arcane, cold, evocation) The frost drake spits a ball of liquid up to 60 feet that explodes into a 20-foot burst cloud of freezing mist. Those in the burst take 8d6 cold damage (DC 25 basic Reflex save). The mist cakes all surfaces in the area with a sheet of slippery ice that turns the area into difficult terrain for 2d4 rounds. It can\u2019t use Freezing Mist Breath again for 1d6 rounds.",
