        text:
          "   (cold, evocation, primal) The frost giant breathes out a 15-foot cone of freezing moisture that quickly condenses into ice, dealing 4d6 cold damage. Each creature in the cone must attempt a DC 28 basic Reflex save. A creature that fails its save is also immobilized and takes 2d6 cold damage at the end of each of its turns until it gets free (Escape DC 28). The giant can\u2019t use Chill Breath again for 1d4 rounds.",
