        text:
          "   (divine, emotion, enchantment, mental) The gancanagh embraces or kisses a willing creature, infusing that creature with their invigorating passion. The creature gains a +1 status bonus to attack rolls and 10 temporary Hit Points for 10 minutes. After that time, the target becomes fatigued for 10 minutes unless they succeed at a DC 21 Fortitude save.",
