        text:
          "   (auditory, emotion, fear, mental, visual) The giant frilled lizard Strides. If it ends that Stride adjacent to at least one other creature, it suddenly displays its colorful neck frills and issues a frightening hiss. Each adjacent creature must succeed at a DC 21 Will save or become frightened 2 (or frightened 3 on a critical failure). The creature is then temporarily immune to Intimidating Display for 1 minute.",
