        name: "Gibbering",
        text:
          " (auditory, aura, emotion, enchantment, incapacitation, mental, occult) Each creature that begins its turn within 60 feet of a gibbering mouther must attempt a DC 19 Will save. On a failure, they are confused for 1 round. On a success, they are temporarily immune for 1 minute. ",
