        text:
          " (disease) Goblins and goblin dogs are immune to goblin pox.  DC 17 Fortitude;  sickened 1 (1 round);  sickened 1 and slowed 1 (1 round);  sickened 2 and can't reduce its sickened value below 1 (1 day).",
