    Wis: " +1, ",
    Cha: " -2",
    "Master Link":
      " (, , ) A homunculus can\u2019t speak, but it is telepathically linked to its creator. It can share information back and forth, including its master\u2019s knowledge and everything the homunculus hears. The range of this link is 1,500 feet. The homunculus adopts the same alignment as its creator and is utterly faithful. If the homunculus is destroyed, the master takes 2d10 mental damage. If the master is slain, the homunculus becomes mindless, claims its current location as its lair, and instinctively attacks anyone who comes near.",
    AC: " 17, ",
