        name: "Critical Failure",
        text:
          "   (auditory, emotion, fear, mental) The owlbear unleashes a loud screech that terrifies its prey. Each creature in an 80-foot emanation must attempt a DC 20 Will save. Regardless of the result, creatures are temporarily immune for 1 minute. Critical Success The creature is unaffected. Success The creature is frightened 1. Failure The creature is frightened 2.  The creature is fleeing for 1 round and frightened 3.",
