        text:
          "   The owlbear has a creature grabbed with its talons. Effect The owlbear attempts to disembowel the creature with a beak Strike. If the Strike hits, the target must attempt a DC 22 Will save. Critical Success The target is unaffected. Success The target is sickened 1. Failure The target is sickened 1 and slowed 1 as long as it remains sickened.",
        actions: "Single Action",
