        text:
          "   (attack, emotion, incapacitation, mental) Frequency once per round; Effect The quelaunt feeds on the emotional unrest of a single creature within 30 feet that\u2019s under a harmful emotion effect. The target must succeed at a DC 37 Will save or take 4d10 mental damage and be stunned for 1 round. If the target fails its saving throw, the quelaunt regains the same number of Hit Points and Feed on Emotion does not cost the quelaunt an action, allowing it to use another action this turn.",
