        text:
          " (divine, necromancy) The remnant of Barzillai draws on the power of haunts, reinforcing his form when they are nearby. His resistance to all damage increases to 12 while he\u2019s within 60 feet of a haunt. When a haunt within 60 feet of Barzillai is disabled, he takes 20 force damage from the backlash.",
