        text:
          "   (evocation, fire, primal) The remorhaz unleashes a gout of flame that deals 8d6 fire damage in a 30-foot cone (DC 26 basic Reflex save). It can\u2019t use Breath Weapon again for 1d4 rounds.",
        actions: "Two Actions",
