        name: "Critical Failure",
        text:
          "   (emotion, enchantment, mental, primal) The rusalka touches a creature within 5 feet using their hand, stirring up memories of regret and shame. The target must attempt a DC 35 Will save. Critical Success The target is unaffected. Success The target is sickened 1. Failure The creature is sickened 1 and stunned 1.  The creature is sickened 1 and stunned 1, and it must use its first action on its next turn to Strike itself, automatically hitting.",
