        name: "Drain Blood",
        text:
          "  Requirement The sabosan has a creature grabbed. Effect The sabosan drains blood from the creature. The creature must succeed at a DC 23 Fortitude save or become drained 1. The sabosan gains a number of temporary Hit Points equal to the number of Hit Points lost by the creature.",
        actions: "Single Action",
