        text:
          " Once per week, a Sphinx can create a magical symbol as though casting a heightened glyph of warding spell. The sphinx usually shapes the glyph to take the form of a written riddle, and sets the password to the answer. A creature that gives the wrong answer or tries to pass without answering must succeed at a DC 26 Will save or suffer one of the following spell effects, chosen by the sphinx when creating the symbol: synaptic pulse (5th), charm (4th), fear (3rd), phantom pain (3rd), sleep (3rd). The sphinx learns the identity of any creature that answers the riddle and tends to be friendly to them.",
