    text:
      "Witchwyrds are four-armed humanoid creatures with hairless blue-gray skin and glowing yellow eyes. They are typically 6-1/2 feet tall and weigh 300 pounds. Their hands possess three evenly sized and spaced digits in a tripod-like arrangement. Witchwyrds conceal most of their faces, leaving only their eyes unmasked. Their inscrutability is a boon to one of their most significant interests, mercantilism, and many haughty witchwyrds openly revel in the befuddlement inspired by their mysterious guises and mannerisms. Witchwyrds have a keen eye for new opportunities and markets, and a witchwyrd almost always knows when someone is trying to pull one over on them., When not trying to blend in with the local community, witchwyrds favor outlandish, loose-fitting clothes in bright reds or yellows and a signature conical hat. Witchwyrds tend to prefer the driest, warmest regions of the areas they visit\u2014perhaps an indicator of their mysterious home world.\r\n",
